import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPenFancy, faTrashAlt } from "@fortawesome/free-solid-svg-icons";
import AddItem from "./AddItem";

export default class TodoList extends Component {
  state = {
    todos: [
      { Id: "I", Title: "This is a simple ReactJS webapp", Status: "Done" },
      { Id: "II", Title: "Styled by BootStrap4", Status: "Pending" },
    ],
  };

  deleteToDo = (todo) => {
    const filteredItems = this.state.todos.filter((x) => x.Id !== todo.Id);
    this.setState({
      todos: filteredItems,
    });
  };

  editToDo = (x) => {
    this.setState((state) => ({
      todos: state.todos.map((todo) => {
        if (todo.Id === x.Id) {
          return {
            ...todo,
            Status: todo.Status === "Done" ? "Pending" : "Done",
          };
        } else {
          return todo;
        }
      }),
    }));
  };

  addToDo = (todo) => {
    this.setState({
      todos: [...this.state.todos, todo],
    });
  };

  render() {
    return (
      <div style={{ margin: "20px" }}>
        <div
          className="jumbotron bg-success text-white"
          style={{ margin: "10px 0px" }}
        >
          <h1> TodoList </h1>
          <p> A simple Todo webapp</p>
        </div>
        <AddItem onAdd = {this.addToDo}></AddItem>
        <table className="table">
          <thead>
            <tr>
              <th>Id</th>
              <th>Title</th>
              <th>Status</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {this.state.todos.map((x) => {
              return (
                <tr key={x.Id}>
                  <td>{x.Id}</td>
                  <td>{x.Title}</td>
                  <td style={{ color: x.Status === "Done" ? "green" : "red" }}>
                    <b>{x.Status}</b>
                  </td>
                  <td>
                    <span>
                      <button
                        className="btn btn-danger"
                        onClick={() => this.deleteToDo(x)}
                      >
                        <div style={{ margin: "0px 3px 0px 3px" }}>
                          <FontAwesomeIcon icon={faTrashAlt}></FontAwesomeIcon>
                        </div>
                      </button>
                      <span> </span>
                      <button
                        className="btn btn-info"
                        onClick={() => this.editToDo(x)}
                      >
                        <div style={{ margin: "0px 2px 0px 2px" }}>
                          <FontAwesomeIcon icon={faPenFancy}></FontAwesomeIcon>
                        </div>
                      </button>
                    </span>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    );
  }
}
