import React, { Component } from "react";
export default class AddItem extends Component {
    state = {
        Id: "",
        Title: "",
        Status: "Pending"
    };

    handleIdChange = (event) => {
        this.setState({
            Id: event.target.value
        });
    };
    handleTitleChange = (event) => {
        this.setState({
            Title: event.target.value
        });
    }
    handleStatusChange = (event) => {
        this.setState({
            Status: event.target.value
        });
    };

    handleOnSubmit = (event) => {
        event.preventDefault();
        this.props.onAdd({
            Id: this.state.Id,
            Title: this.state.Title,
            Status: this.state.Status
        });
        this.setState({
            Id: "",
            Title: "",
            Status: "Pending"
        });
    };

    render() {
        return (
            <div style={{ padding: '0px 15px 15px 15px' }}>
                <h5>
                    <b>Add An Item</b>
                </h5>
                <form onSubmit = {this.handleOnSubmit}>
                    <div className="form-group" >
                        {/* value binding */}
                        <input value={this.state.Id} className="form-control" onChange={this.handleIdChange} placeholder="Enter Id" />
                    </div>
                    <div className="form-group" >
                        <input value={this.state.Title} className="form-control" onChange={this.handleTitleChange} placeholder="Enter Title" />
                    </div>
                    <div className="form-group">
                        <select value={this.state.Status} onChange={this.handleStatusChange} className="form-control" >
                            <option value="Done" >Done</option>
                            <option value="Pending" >Pending</option>
                        </select>
                    </div>
                    <button type="submit" className="form-control btn btn-success" >Add Todo</button>
                </form>
            </div>
        );
    }
}